"use client";

import { useMemo, useState } from "react";
import { FacultyAuditPortfolio } from "./FacultyAuditPortfolio";
import { DashboardHeader } from "./DashboardHeader";
import { StatsOverview } from "./StatsOverview";
import { PendingReviewsAlert } from "./PendingReviewsAlert";
import { ReviewStatistics } from "./ReviewStatistics";
import { RecentActivity } from "./RecentActivity";
import { FacultySubmissionStatus } from "./FacultySubmissionStatus";
import { DashboardStats, FacultyMember, RecentReview } from "./types";
import auditorDashboards from "@/data/dashboards/auditor.json";

interface AuditorDashboardRecord {
  stats: DashboardStats;
  facultyMembers: FacultyMember[];
  recentReviews: RecentReview[];
}

export function AuditorDashboard() {
  const [selectedFaculty, setSelectedFaculty] = useState<FacultyMember | null>(
    null,
  );
  const { stats, facultyMembers, recentReviews } = useMemo(() => {
    const dashboards = auditorDashboards as AuditorDashboardRecord[];
    const dashboard = dashboards[0];
    return {
      stats: dashboard?.stats ?? dashboards[0].stats,
      facultyMembers: dashboard?.facultyMembers ?? dashboards[0].facultyMembers,
      recentReviews: dashboard?.recentReviews ?? dashboards[0].recentReviews,
    };
  }, []);

  if (selectedFaculty) {
    return (
      <FacultyAuditPortfolio
        faculty={selectedFaculty}
        onBack={() => setSelectedFaculty(null)}
      />
    );
  }

  return (
    <div className="space-y-6">
      <DashboardHeader />
      <StatsOverview stats={stats} />
      <PendingReviewsAlert stats={stats} />

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <ReviewStatistics stats={stats} />
        <RecentActivity reviews={recentReviews} />
      </div>

      <FacultySubmissionStatus
        facultyMembers={facultyMembers}
        onSelectFaculty={setSelectedFaculty}
      />
    </div>
  );
}
